.wish {
  padding: 205px 15% 550px;
  background-image: url(../img/wish-bg-desktop.png), url(../img/girl_flag-desktop.png);
  background-repeat: no-repeat;
  background-position: -8px 146px, right 0px;
  background-size: 167px 226px, 60% auto; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .wish {
      background-image: url(../img/wish-bg-desktop@2x.png), url(../img/girl_flag-desktop@2x.png); } }

.wish h2 {
  margin-bottom: 17px; }

.wish p {
  width: 45%;
  margin: 0 0 59px;
  font-size: 1.0625rem; }

.wish [type="text"] {
  display: none; }

.form {
  display: grid;
  width: 50%;
  font-size: 1.0625rem;
  font-variant: small-caps;
  grid-template-columns: 1fr 45%; }

.form [type="tel"] {
  padding: 21px 47% 16px 41px;
  border: 2px solid #e3e3e3;
  border-radius: 35px;
  grid-column: 1/-1;
  grid-row: 1/2; }
  .form [type="tel"]:focus {
    border: 2px solid #ffd701;
    outline: none; }
  .form [type="tel"]:invalid:not(:placeholder-shown) {
    border-color: #ff0000; }
  .form [type="tel"]:valid {
    border-color: #484848; }
  .form [type="tel"] + span {
    position: relative; }
    .form [type="tel"] + span:before {
      bottom: 10px;
      left: 10px; }
  .form [type="tel"]:invalid:not(:placeholder-shown) + span:before {
    content: "Ошибка: неверный формат";
    padding-left: 35px;
    color: #ff0000;
    font-size: 0.8265rem; }

.form button {
  z-index: 2;
  color: #ffffff;
  font-family: inherit;
  font-variant: inherit;
  background-image: linear-gradient(140.64deg, #ffc341 20.46%, #ffd701 65.31%);
  border-radius: 80px;
  grid-column: 2/-1;
  grid-row: 1/2; }
  .form button:hover, .form button:focus {
    background-image: linear-gradient(143.55deg, #ffc341 -50.3%, #ffd701 63%); }
  .form button:active {
    background-image: radial-gradient(150% 119.49% at 50% 50.85%, rgba(0, 0, 0, 0) 30.67%, rgba(3, 3, 3, 0.24) 100%), linear-gradient(143.55deg, #FFC341 -50.3%, #FFD701 63.1%); }

